.container {
  margin: auto;
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 90%;
  padding: 20px;
  color: var(--secondary);
  font-family: Arial;
  background: rgba(255, 255, 255, .1);
  box-shadow: 0 5px 15px rgba(0,0,0,.1);
  z-index: 1;
  border-radius: 8px;
  backdrop-filter: blur(10px);
}

.title {
  font-size: 30px;
  margin: 5px;
}

@media screen and (min-width: 0px) {
  body {
    --primary: #ce0d31;
    --secondary: #f6f7ee;
    background: #1d061a;
    padding: 10px;
  }

  main {
    padding: 20px;
    width: 500px;
    margin: auto;
    background: linear-gradient(360deg, #202020, #de1d41);
    border-radius: 8px;
    z-index: 0;
  }

  .audioPlayer {
    margin: 10px;
    color: var(--secondary);
    align-items: center;
    display: flex;
    width: 700px;
  }
} 

@media screen and (min-width: 618px) {
  body {
    --primary: #ce0d31;
    --secondary: #f6f7ee;
    background: #1d061a;
    padding: 30px;
  }

  main {
    padding: 20px;
    width: 80%;
    margin: auto;
    background: linear-gradient(360deg, #202020, #de1d41);
    border-radius: 8px;
    z-index: 0;
  }

  .audioPlayer {
    margin: 35px;
    color: var(--secondary);
    align-items: center;
    display: flex;
    width: 700px;
  }
} 

.forwardBackward {
  background: none;
  border: none;
  display: flex;
  align-items: center;
  font-family: monospace;
  font-size: 30px;
  cursor: pointer;
  color: var(--secondary);
}

.forwardBackward:hover {
  color: var(--primary);
}

.playPause {
  background: var(--primary);
  border: none;
  border-radius: 50%;
  width: 75px;
  height: 75px;
  font-size: 40px;
  color: var(--secondary);
  display: flex;
  justify-content: center;
  align-items: center;
}

.play {
  position: relative;
  left: 2px;
}

.currentTime,
.duration {
  font-family: Arial;
  font-size: 18px;
  margin-left: 10px;
  margin-right: 10px;
}

.currentTime {
  margin-left: 25px;
}

.progressBar {
  --bar-bg: rgba(255,255,255,.1);
  --seek-before-width: 0;
  --seek-before-color: var(--primary);
  --knobby: #404040;
  --selectedKnobby: #202020;

  appearance: none;
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 11px;
  outline: none;
}

.progressBar::-webkit-slider-runnable-track {
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 11px;
  outline: none;
}

.progressBar::-moz-range-track {
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 11px;
  outline: none;
}

.progressBar::-moz-focus-outer {
  border: 0;
}

.progressBar::before {
  content: '';
  height: 11px;
  width: var(--seek-before-width);
  background-color: var(--seek-before-color);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  cursor: pointer;
}

.progressBar::-moz-range-progress {
  background-color: var(--seek-before-color);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  height: 11px;
}

.progressBar::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  border: none;
  background-color: var(--knobby);
  cursor: pointer;
  position: relative;
  margin: -2px 0 0 0;
  z-index: 3;
  box-sizing: border-box;
}

.progressBar:active::-webkit-slider-thumb {
  transform: scale(1.2);
  background: var(--selectedKnobby);
}

.progressBar::-moz-range-thumb {
    height: 15px;
    width: 15px;
    border-radius: 50%;
    border: transparent;
    background-color: var(--knobby);
    cursor: pointer;
    position: relative;
    z-index: 3;
    box-sizing: border-box;
}

.progressBar:active::-moz-range-thumb {
  transform: scale(1.2);
  background: var(--selectedKnobby);
}

.bold {
  font-weight: bolder;
  color: #ccc;
}

.description h1 {
  font-size: 20px;
}

.description {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 10px;
}

.description p {
  color: #aaa;
  text-align: justify;
}